//     
 // element - ,     , direction -   (previous  next)

const findSiblingElements = (element, direction) => {
    const siblings = []; //     
    let sibling = direction === 'previous' ? element.previousElementSibling : element.nextElementSibling; //   

    while (sibling) { //    
        siblings.push(sibling); //    
        sibling = sibling[direction + 'ElementSibling']; //   
    }

    return siblings; //      
};

const findPrevElements = (element) => findSiblingElements(element, 'previous'); //      
const findNextElements = (element) => findSiblingElements(element, 'next'); //      

