// hrefTo:  DOM,     
// triggerTo:  -,      
// yOffset:      
const scrollToElement = (hrefTo, triggerTo = null, yOffset = { default: 0 }) => {
    let element;
    //  hrefTo  ,  ,  ,   DOM-
    if (typeof hrefTo === 'string') {
        element = document.querySelector(hrefTo);
    } else if (hrefTo instanceof HTMLElement) {
        element = hrefTo;
    } else {
        console.error('scrollToElement:    .');
        return;
    }
    
    let trigger;
    if (triggerTo) {
        if (typeof triggerTo === 'string') {
            trigger = document.querySelector(triggerTo);
        } else if (triggerTo instanceof HTMLElement) {
            trigger = triggerTo;
        } else {
            console.error('scrollToElement:    trigger.');
            return;
        }
    }
    
    if (!element) {
        console.error('scrollToElement:   .');
        return;
    }

    let y;

    //      
    if (window.matchMedia("(max-width: 767px)").matches && yOffset.mobile !== undefined) {
        y = element.getBoundingClientRect().top + window.scrollY + yOffset.mobile;
    } else if (window.matchMedia("(min-width: 768px) and (max-width: 1200px)").matches && yOffset.tablet !== undefined) {
        y = element.getBoundingClientRect().top + window.scrollY + yOffset.tablet;
    } else if (window.matchMedia("(min-width: 1200px)").matches && yOffset.desktop !== undefined) {
        y = element.getBoundingClientRect().top + window.scrollY + yOffset.desktop;
    } else {
        //  yOffset  ',   default
        y = element.getBoundingClientRect().top + window.scrollY + (typeof yOffset === 'object' ? yOffset.default : yOffset);
    }

    //  trigger ,   click, , , / 
    if (trigger) {
        trigger.click();
    }

    window.scrollTo({
        top: y,
        behavior: "smooth"
    });
};


