const removeErrorStyle = () => {
    // ,   input    error_style         id
    const form = document.querySelector('form'); //  'form'    ,  

    if (!form) return; //     ,   

    //     error_style   input
    const removeErrorClass = (input) => {
        if (input.classList.contains('error_style')) {
            input.classList.remove('error_style');
        }
    };

    //    
    const handleEvent = (event) => {
        // ,      input   error_style
        const target = event.target;
        if (target.tagName === 'INPUT' && target.classList.contains('error_style')) {
            removeErrorClass(target);
        }
    };

    //        'input'  'click'   
    form.addEventListener('input', handleEvent);
    form.addEventListener('click', handleEvent);
};

