//  dropdownToggle,   /      .      ,    .
const dropdownToggle = () => {
    const overlay = document.getElementById('overlay'),
          dropdownToggles = document.querySelectorAll('.ds-dropdown-toggle'),
          dropdownBoxes = document.querySelectorAll('.ds-dropdown-box'),
          header = document.querySelector('.ds-header'),
          width = window.innerWidth,
          isTouchDevice = 'ontouchstart' in window || navigator.maxTouchPoints > 0 || navigator.msMaxTouchPoints > 0;

    //      
    const toggleActive = (element, isActive) => {
        if (isActive) {
            element.classList.add('active');
        } else {
            element.classList.remove('active');
        }
    };

    // ,     
    const closeAllDropdowns = () => {
        dropdownBoxes.forEach(dropdown => {
            toggleActive(dropdown, false);
        });
        toggleActive(overlay, false);
    };

    //     
    document.addEventListener('click', event => {
        const { target } = event;
        const dropdownToggle = target.closest('.ds-dropdown-toggle');
        
        if (dropdownToggle && (width < 1200 || isTouchDevice)) {
            event.preventDefault();
            const parentDropdown = dropdownToggle.parentNode;

            // ,    dropdown
            const isActive = parentDropdown.classList.contains('active');

            //   dropdowns
            closeAllDropdowns();

            //    ,  
            if (!isActive) {
                toggleActive(parentDropdown, true);
                toggleActive(overlay, true);
            }
        } else if (target === overlay || !header.contains(target)) {
            closeAllDropdowns();
        }
    });

    //        
    if (window.innerWidth > 1199 && !isTouchDevice) {
        dropdownBoxes.forEach(box => {
            const activateBox = () => toggleActive(box, true);
            const deactivateBox = () => toggleActive(box, false);

            box.addEventListener('mouseenter', activateBox);
            box.addEventListener('mouseleave', deactivateBox);
        });
    }
};

