function openMobileSearch() {
    const searchBtn = document.querySelector('.ds-header-search-toggle-button');
    const searchBox = document.querySelector('.ds-header-search');
    const searchInput = document.querySelector('.ds-header-search .form-control');
    const searchClose = document.querySelector('.ds-header-search-close');
    const liveSearch = document.getElementById('ds_livesearch');
    const infoBar = document.getElementById('oct-infobar');
    const width = window.innerWidth;
    const header = document.querySelector('.ds-header');
    const headerHeight = header.offsetHeight;
    const dropdown = document.querySelectorAll('.ds-dropdown-box');

    searchBtn.addEventListener('click', () => {
        searchBox.classList.add('active');
        searchInput.focus();
        liveSearch.classList.add('expanded');
        document.body.classList.add('no-scroll');

        dropdown.forEach(item => {
            if (item && item.classList.contains('active')) {
                item.classList.remove('active');
            }
        });
    });

    searchClose.addEventListener('click', () => {
        searchBox.classList.remove('active');
        clearLiveSearch();
    });

    if (infoBar && width < 768) {
        const observer = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    let infoBarHeight = entry.target.offsetHeight;
                    searchBox.style.top = `${infoBarHeight + 5}px`;
                    liveSearch.style.top = `${infoBarHeight + headerHeight}px`;
                } else {
                    searchBox.style.top = '';
                    liveSearch.style.top = '';
                }
            });
        }, {threshold: 1.0});

        observer.observe(infoBar);
    }
    
    if (width < 768 && !liveSearch.length) {
        $('#ds_livesearch_mobile').append(liveSearch);
    }

    if (width < 768 && !infoBar) {
        searchBox.style.height = `${headerHeight - 1}px`;
        liveSearch.style.top = `${headerHeight}px`;
    }
    
}

