function mobileMenu() {
    const sidebar = document.getElementById('ds_sidebar'),
        sidebarClose = document.querySelectorAll('[data-sidebar="close"]'),
        catalogToggleButtons = document.querySelectorAll('[data-sidebar="catalog"], .ds-header-catalog-button'),
        catalogMenu = document.querySelector('.ds-menu-main-catalog'),
        catalogMenuClose = document.querySelectorAll('[data-sidebar="catalogclose"]'),
        menuToClose = document.querySelectorAll('.ds-menu-catalog.active'),
        menuToggle = document.querySelectorAll('.ds-menu-catalog-item'),
        menuBack = document.querySelectorAll('[data-sidebar="catalogback"]'),
        sidebarMenuInner = document.querySelectorAll('.ds-sidebar-menu-inner'),
        sidebarMenuToggle = document.querySelectorAll('[data-sidebar="menuinner"]'),
        overlay = document.getElementById('overlay'),
        menuMediaQuery = window.matchMedia('(max-width: 1199.98px)');

    function closeSidebar() {
        if (sidebar) {
            sidebar.classList.remove('active');
        }
        overlay.classList.remove('active');
        document.body.classList.remove('no-scroll');

        sidebarMenuInner.forEach(item => {
            item.classList.remove('active');
        });
    }

    function closeMenu() {
        catalogMenu.classList.remove('active');
        document.body.classList.remove('no-scroll');
        overlay.classList.remove('active');

        menuToClose.forEach(item => {
            item.classList.remove('active');
        });
    }

    catalogToggleButtons.forEach(item => {
        item.addEventListener('click', (e) => {
            initializeCatalogMenu();

            if (menuMediaQuery.matches) {
                catalogMenu.classList.add('active');
                document.body.classList.add('no-scroll');
                overlay.classList.add('active');
            } else {
                catalogMenu.classList.toggle('active');
                document.body.classList.toggle('no-scroll');
                overlay.classList.toggle('active');
            }
        });
    });

    function initializeCatalogMenu() {
        if (menuMediaQuery.matches) {

            menuToggle.forEach(item => {
                const menuToShow = item.querySelector('.ds-menu-catalog');

                item.addEventListener('click', (e) => {
                    const target = e.target;

                    if ((!target.classList.contains('ds-sidebar-close')) && (!target.classList.contains('ds-sidebar-header-back')) && (target.tagName.toLowerCase() !== 'a')) {
                        menuToShow.classList.add('active');
                    }
                });
            });

            sidebarMenuToggle.forEach(item => {
                const menuToShow = item.querySelector('.ds-sidebar-menu-inner');

                item.addEventListener('click', (e) => {
                    const target = e.target;

                    if ((!target.classList.contains('ds-sidebar-close')) && (!target.classList.contains('ds-sidebar-header-back')) && (target.tagName.toLowerCase() !== 'a')) {
                        menuToShow.classList.add('active');
                    }
                });
            });

            menuBack.forEach(item => {
                item.addEventListener('click', (e) => {
                    let target = e.currentTarget;

                    if (target !== item) {
                        target = target.parentNode;
                    }

                    //       ds-menu-catalog
                    const parentElement = target.closest('.ds-menu-catalog, .ds-sidebar-menu-inner');

                    if (parentElement) {
                        //   active
                        parentElement.classList.remove('active');
                    }

                });
            });

            sidebarClose.forEach(item => {
                item.addEventListener('click', () => {
                    closeSidebar();
                });
            });

            catalogMenuClose.forEach(item => {
                item.addEventListener('click', () => {
                    closeMenu();
                });
            });
        }

        overlay.addEventListener('click', () => {
            closeSidebar();
            closeMenu();
        });
    }
}

