//  horizontalMenu       

function horizontalMenu() { 
    const categoriesMenu = document.querySelector('.ds-menu-maincategories'),
        mobileMenuBox = document.querySelector('.ds-menu-catalog-items'),
        desktopMenuBox = document.querySelector('.ds-menu-maincategories-desktop-box > ul'),
        menuMediaQuery = window.matchMedia('(max-width: 1199.98px)'),
        categoriesMenuItems = categoriesMenu.querySelectorAll('.ds-menu-maincategories-item');
    let isMobileMenuPopulated = false; //    

    if (!categoriesMenu) return;

    //  ,      -
    function handleTabletChange(e) {
        if (e.matches && !isMobileMenuPopulated) {
            desktopMenuBox.style.setProperty('display', 'none', 'important');
            // copy categoriesMenuItems to mobileMenuBox and renove all classes than add classes .ds-menu-catalog-item .d-flex .align-items-center .justify-content-between .fsz-14 .dark-text
            categoriesMenuItems.forEach(item => {
                let itemsDropdowns = item.querySelectorAll('.ds-menu-maincategories-dropdown'),
                    itemsDropdownsLinks = item.querySelectorAll('.ds-menu-maincategories-item-title');

                item.classList.remove('py-2', 'px-3', 'px-xl-2');
                item.classList.add('ds-menu-catalog-item', 'justify-content-between', 'fsz-14', 'dark-text');
                
                itemsDropdownsLinks.forEach(link => {
                    link.classList.remove('ds-menu-maincategories-item-title', 'fsz-14', 'dark-text');
                    link.classList.add('ds-menu-catalog-item-title', 'd-flex', 'align-items-center');  
                });

                itemsDropdowns.forEach(dropdown => {
                    dropdown.classList.remove('position-absolute');
                    dropdown.classList.add('ds-menu-catalog');
                });

                mobileMenuBox.appendChild(item);

            });
            isMobileMenuPopulated = true; //     
            mobileMenu(); //   mobileMenu     
        } else if (!e.matches) {
            categoriesMenuItems.forEach(item => {
                desktopMenuBox.appendChild(item); //   

                //  
                item.classList.remove('ds-menu-catalog-item', 'justify-content-between', 'fsz-14', 'dark-text');
                item.classList.add('py-2', 'px-3', 'px-xl-2');

                let itemsDropdowns = item.querySelectorAll('.ds-menu-maincategories-dropdown'),
                    itemsDropdownsLinks = item.querySelectorAll('.ds-menu-catalog-item-title');

                itemsDropdownsLinks.forEach(link => {
                    link.classList.remove('ds-menu-catalog-item-title', 'd-flex', 'align-items-center');
                    link.classList.add('ds-menu-maincategories-item-title', 'fsz-14', 'dark-text');
                });

                itemsDropdowns.forEach(dropdown => {
                    dropdown.classList.remove('ds-menu-catalog');
                    dropdown.classList.add('position-absolute');
                });
            });

            desktopMenuBox.style.removeProperty('display');

            function alignDropdowns() {
                // ,  categoriesMenu  desktopMenuBox 
                if (!categoriesMenu || !desktopMenuBox) return;
            
                categoriesMenuItems.forEach((item) => {
                    const dropdown = item.querySelector('.ds-menu-maincategories-dropdown-narrow');
                    
                    if (dropdown) {
                        const itemRect = item.getBoundingClientRect();
                        const menuRect = categoriesMenu.getBoundingClientRect();
                        
                        //     
                        //   item.offsetLeft,    left  itemRect,       
                        const correctLeftPosition = itemRect.left - menuRect.left; //  
            
                        //  dropdown
                        dropdown.style.left = `${correctLeftPosition}px`;
                        dropdown.style.position = 'absolute'; // ,    
                    }
                });
            }
            
            //     dropdowns   
            alignDropdowns();
            
            //         
            window.addEventListener('resize', alignDropdowns);
        }
    }

    //      
    handleTabletChange(menuMediaQuery);

    //     -
    menuMediaQuery.addListener(handleTabletChange);

    //     categoriesMenu
    const lastItem = categoriesMenu.querySelector(':last-child');

    //   IntersectionObserver
    const observerOptions = {
        root: desktopMenuBox,
        rootMargin: "0px",
        threshold: 0.1 // 10% 
    };

    //   Observer
    const observer = new IntersectionObserver((entries, observer) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                //     ,  
                categoriesMenu.classList.remove('hide-gradient');
            } else {
                //   ,  
                categoriesMenu.classList.add('hide-gradient');
            }
        });
    }, observerOptions);

    //  
    if (lastItem) {
        observer.observe(lastItem);
    }
}

