async function fetchViewedProducts() {
    try {
        masked('#ds_sidebar_viewed', true); //  
        const response = await fetch("index.php?route=octemplates/main/oct_functions/productViews", {
            method: "POST",
            headers: {
                'X-Requested-With': 'XMLHttpRequest'
            }
        });

        if (!response.ok) {
            throw new Error('Network response was not ok');
        }

        const data = await response.text();
        document.querySelector('#ds_sidebar_viewed .ds-sidebar-content').innerHTML = data;
        setCartBtnAdded();
    } catch (error) {
        console.error('There was a problem with the fetch operation:', error);
    } finally {
        masked('#ds_sidebar_viewed', false); //  
    }
}

function toggleSidebar(active) {
    const viewedProductsSidebar = document.getElementById('ds_sidebar_viewed'),
          overlay = document.getElementById('overlay');

    if (!viewedProductsSidebar) return;

    if (active) {
        viewedProductsSidebar.classList.add('active');
        overlay.classList.add('active', 'active-sidebar');
        document.body.classList.add('no-scroll');
    } else {
        viewedProductsSidebar.classList.remove('active');
        overlay.classList.remove('active', 'active-sidebar');
        document.body.classList.remove('no-scroll');
    }
}

function setupViewedProductsSidebar() {
    const viewedProductsToggle = document.querySelectorAll('[data-viewed="sidebar"]'),
          sidebarClose = document.querySelectorAll('[data-sidebar="close"]'),
          overlay = document.getElementById('overlay');
    let contentLoaded = false; //     

    if (!viewedProductsToggle.length) {
        return;
    }

    viewedProductsToggle.forEach(item => {
        item.addEventListener('click', async () => {
            toggleSidebar(true);
            if (!contentLoaded) {
                await fetchViewedProducts(); //    
                contentLoaded = true;
            }
        });
    });

    overlay.addEventListener('click', () => toggleSidebar(false));
    sidebarClose.forEach(item => item.addEventListener('click', () => toggleSidebar(false)));
}

function getOCTViewedCookie(name) {
    let value = "; " + document.cookie;
    let parts = value.split("; " + name + "=");
    return parts.length === 2 ? parts.pop().split(";").shift() : null;
}
