//  slideshowPlus      ,        

function slideshowPlus() {
    const overlay = document.getElementById('overlay'),
        header = document.querySelector('.ds-header'),
        slideshow = document.querySelector('.ds-slideshow-items'),
        slideshowParrent = document.querySelector('.ds-slideshow'),
        catalogMenu = document.querySelector('.ds-menu-main-catalog'),
        menuMediaQuery = window.matchMedia('(min-width: 1200px)'),
        body = document.body;

    let catalogMenuObserver;

    if (!slideshow) {
        return;
    }

    //    mouseenter  mouseleave
    function handleMouseEnter() {
        overlay.classList.add('active');
        body.classList.add('no-scroll');
    }

    function handleMouseLeave() {
        overlay.classList.remove('active');
        body.classList.remove('no-scroll');
    }

    //  ' slideshow  
    //   media query
    function handleMediaChange() {
        if (menuMediaQuery.matches) {
            //  ' slideshow  
            const catalogMenu = document.querySelector('.ds-menu-main-catalog'),
                catalogMenuObserverOptions = {
                root: null, //   
                rootMargin: "0px",
                threshold: 0.1 //   10% 
            };

            catalogMenuObserver = new IntersectionObserver((entries) => {
                entries.forEach(entry => {
                    if (entry.isIntersecting) {
                        catalogMenu.classList.add('active', 'with-slideshow');
                        header.classList.remove('sticky-top');

                        // catalogMenu min-height = slideshow height
                        catalogMenu.querySelector('.ds-menu-catalog-items').style.minHeight = slideshowParrent.offsetHeight + 'px';

                        //  catalogMenu   slideshow
                        slideshow.parentElement.style.minHeight = catalogMenu.offsetHeight + 'px';

                        if (catalogMenu.classList.contains('with-slideshow')) {
                            catalogMenu.addEventListener('mouseenter', handleMouseEnter);
                            catalogMenu.addEventListener('mouseleave', handleMouseLeave);
                        }
                    } else {
                        catalogMenu.removeEventListener('mouseenter', handleMouseEnter);
                        catalogMenu.removeEventListener('mouseleave', handleMouseLeave);
                        catalogMenu.classList.remove('active', 'with-slideshow');
                        header.classList.add('sticky-top');
                    }
                });
            }, catalogMenuObserverOptions);

            catalogMenuObserver.observe(slideshow);
        } else {
            if (catalogMenuObserver) {
                if (catalogMenu && catalogMenu.classList.contains('with-slideshow')) {
                    catalogMenu.classList.remove('active', 'with-slideshow');
                }

                header.classList.add('sticky-top');

                catalogMenuObserver.disconnect();
            }
        }
    }

    //   / IntersectionObserver
    handleMediaChange();

    //       
    menuMediaQuery.addEventListener('change', handleMediaChange);
}

