function toggleColumnCategories() {
    const menuToggle = document.querySelectorAll('.ds-column-categories-item > span'),
        menuLinks = document.querySelectorAll('.ds-column-categories a');

    if (!menuToggle) return;

    menuToggle.forEach(element => {
        // ,     
        if (!element.dataset.listenerAdded) {
            const menuToShow = element.closest('li').querySelector('.ds-column-categories-children');

            element.addEventListener('click', function (e) {
                const target = e.target;

                if ((target.tagName.toLowerCase() !== 'a') && menuToShow) {
                    this.closest('li').classList.toggle('clicked');
                    menuToShow.classList.toggle('active');
                }
            });

            //    ,    
            element.dataset.listenerAdded = 'true';
        }
    });

    menuLinks.forEach(element => {
        const href = element.getAttribute('href');

        if (href === window.location.href) {
            element.classList.add('current');
        }
    });
}

