function switchCategoryDisplay() {
    const listBtn = document.getElementById('list-view'),
        gridBtn = document.getElementById('grid-view'),
        priceBtn = document.getElementById('price-view');

    const productBlocks = document.querySelectorAll('.product-layout');
    const buttons = document.querySelectorAll('.ds-category-appearance-btn');

    if (!buttons) return;

    const classMap = {
        'list-view': ['product-list', 'col-12', 'col-sm-6', 'col-md-12'],
        'grid-view': ['product-grid', 'col-sm-6', 'col-md-4', 'col-lg-3'],
        'price-view': ['product-price', 'col-12', 'col-sm-6', 'col-md-12']
    };

    function clearClasses(item) {
        item.classList.remove('product-grid', 'product-list', 'product-price', 'col-12', 'col-sm-6', 'col-md-4', 'col-md-12', 'col-lg-3');
    }

    function buttonsAction(button) {
        buttons.forEach(item => item.classList.remove('active'));
        button.classList.add('active');

        productBlocks.forEach(item => {
            clearClasses(item);

            const classesToAdd = classMap[button.id];
            if (classesToAdd) {
                item.classList.add(...classesToAdd);
                localStorage.setItem('display', button.id.split('-')[0]);
            }
        });
    }

    const storedDisplay = localStorage.getItem('display');
    const defaultBtn = storedDisplay ? document.getElementById(`${storedDisplay}-view`) : gridBtn;
    buttonsAction(defaultBtn);

    buttons.forEach(button => {
        button.addEventListener('click', function () {
            buttonsAction(this);
        });
    });
}

