//    wishlist  compare
function setAddedCompareWishlist() {
    //         data (data-compare-ids, data-wishlist-ids)      
    const compareElement = document.querySelector('[data-compare-ids]');
    const wishlistElement = document.querySelector('[data-wishlist-ids]');

    if (!compareElement || !wishlistElement) return;

    //   id       data
    let compareIds = compareElement.getAttribute('data-compare-ids').split(',');
    let wishlistIds = wishlistElement.getAttribute('data-wishlist-ids').split(',');

    //      (/ )  title  (  /  )    (compare/wishlist)    (/ )
    const toggleButtonState = (btnClass, operation, id, isAdded) => {
        //   
        const buttons = document.querySelectorAll(`.${btnClass}[onclick="${operation}.${isAdded ? 'add' : 'remove'}('${id}');"]`);
        //    title     (compare/wishlist)
        const element = operation === 'compare' ? compareElement : wishlistElement;
        //    title      (/ )
        buttons.forEach(button => {
            button.setAttribute('onclick', `${operation}.${isAdded ? 'remove' : 'add'}('${id}');`);
            button.classList[isAdded ? 'add' : 'remove']('added');
            const titleText = element.getAttribute(isAdded ? `data-${operation}-text-in` : `data-${operation}-text`);
            button.setAttribute('title', titleText);
        });
    };

    //    
    compareIds.forEach(id => toggleButtonState('ds-compare-btn', 'compare', id, true));
    wishlistIds.forEach(id => toggleButtonState('ds-wishlist-btn', 'wishlist', id, true));

    //      
    const compareButtons = document.querySelectorAll('.ds-compare-btn');
    const wishlistButtons = document.querySelectorAll('.ds-wishlist-btn');

    //           data  id
    [compareButtons, wishlistButtons].forEach(buttons => {
        //      
        const operation = buttons === compareButtons ? 'compare' : 'wishlist';
        const ids = operation === 'compare' ? compareIds : wishlistIds;
        const element = operation === 'compare' ? compareElement : wishlistElement;

        buttons.forEach(button => {
            //       
            button.addEventListener('click', () => {
                const onclickAttr = button.getAttribute('onclick') || '';
                const idMatch = onclickAttr.match(/\d+/);

                if (!idMatch) return;

                //  id   onclick  
                const id = idMatch[0];
                const isAdded = button.classList.contains('added');

                //   id
                const updatedIds = isAdded ?
                    ids.filter(itemId => itemId !== id) :
                    [...ids, id];

                //   
                toggleButtonState(`ds-${operation}-btn`, operation, id, !isAdded);

                //   data
                element.setAttribute(`data-${operation}-ids`, updatedIds.join(','));

                //    ids
                if (operation === 'compare') {
                    compareIds = updatedIds;
                } else {
                    wishlistIds = updatedIds;
                }
            });
        });
    });
}

