// ,     top  sticky       header

function stickyProduct() {
    const product = document.querySelector('.ds-product-block');
    const tabs = document.querySelector('.ds-product-tabs');
    const header = document.querySelector('.ds-header');

    if (!header || !tabs) return;

    const updatePositions = () => {
        const headerHeight = header.offsetHeight;

        if (tabs) {
            tabs.style.top = headerHeight + 'px';

            if (product) {
                const tabsHeight = tabs.offsetHeight;
                product.style.top = (headerHeight + tabsHeight + 16) + 'px';
            }
        } else {
            if (product) {
                product.style.top = (headerHeight + 16) + 'px';
            }  
        } 
    };

    //   
    updatePositions();

    //       
    window.addEventListener('resize', updatePositions);
}

// ======================================================

//           

function octProductTabs() {
    let activeTabElements = [],
        manualTabActivation = false,
        tabParrent = document.getElementById('oct-tabs'),
        tabs = tabParrent.querySelectorAll('.ds-product-tabs-item'),
        reviewsTabLink = tabParrent.querySelector('[data-tab-target=".ds-product-reviews"]'),
        reviewsScrollLink = document.querySelector('.ds-scroll-to-reviews'),
        header = document.querySelector('.ds-header'),
        headerHeight = header.offsetHeight,
        tabsHeight = tabParrent.offsetHeight;

    if (!tabParrent) return;

    // ===   (initProductObserver, setActiveTab, scrollToTabCenter)    ===
    function initProductObserver(tabs, tabTargets) {
        const height = header.offsetHeight + tabsHeight;
        const options = {
            root: null,
            rootMargin: `-${height}px 0px -45% 0px`,
            threshold: 0
        };

        const observer = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    if (!activeTabElements.some(el => el === entry.target)) {
                       activeTabElements.push(entry.target);
                    }
                } else {
                    const index = activeTabElements.findIndex(el => el === entry.target);
                    if (index > -1) {
                        activeTabElements.splice(index, 1);
                    }
                }
            });

            if (activeTabElements.length === 0) return;

            const topElement = activeTabElements.reduce((highest, current) => {
                if (!highest) return current;
                return highest.getBoundingClientRect().top < current.getBoundingClientRect().top ? highest : current;
            }, null);

            //    
            if (topElement && !manualTabActivation) {
                const topElementClass = topElement.classList[0];
                setActiveTab(tabs, topElementClass);
                
                const correspondingTab = tabParrent.querySelector(`[data-tab-target=".${topElementClass}"]`);
                if (correspondingTab) {
                    scrollToTabCenter(correspondingTab, tabParrent);
                }
            }
        }, options);

        tabTargets.forEach(target => {
            observer.observe(target);
        });
    }

    function setActiveTab(tabs, targetClass) {
        tabs.forEach(tab => {
            const dataTarget = tab.getAttribute('data-tab-target').replace('.', '');
            if (dataTarget === targetClass) {
                tab.classList.add('active');
            } else {
                tab.classList.remove('active');
            }
        });
    }
    
    function scrollToTabCenter(tabElement, tabParent) {
        // ...    
        let sum = 0,
            prevAll = findPrevElements(tabElement);

        prevAll.forEach(item => {
            sum += item.offsetWidth;
        });

        let dist = sum - (tabParent.offsetWidth - tabElement.offsetWidth) / 2;

        if (dist < 0) {
            dist = 0;
        }

        tabParent.scroll({
            left: dist,
            behavior: 'smooth'
        });
    }

    // ===      ===
    const userInteractionEvents = ['wheel', 'touchstart', 'keydown'];

    function resetManualActivation() {
        manualTabActivation = false;
        //  ,      '
        userInteractionEvents.forEach(event => {
            window.removeEventListener(event, resetManualActivation);
        });
    }
    
    // ===     ===
    tabs.forEach(item => {
        item.addEventListener('click', (e) => {
            // 1.  
            manualTabActivation = true;
    
            const target = item; 
            const tabTarget = target.getAttribute("data-tab-target");
    
            if (target.classList.contains('active') && tabTarget !== ".ds-product-tab-main-content") {
                //      ,  observer'  
                resetManualActivation();
                return;
            }
    
            scrollToTabCenter(target, tabParrent);
    
            if (!target.classList.contains('active')) {
                tabs.forEach(tab => tab.classList.remove('active'));
                target.classList.add('active');
            }
    
            scrollToElement(`${tabTarget}`, null, {
                default: -headerHeight - tabsHeight - 42,
                mobile: -headerHeight - tabsHeight - 42,
                tablet: -headerHeight - tabsHeight - 42,
                desktop: -headerHeight - tabsHeight - 42
            });
    
            // 2.  ,     
            userInteractionEvents.forEach(event => {
                window.addEventListener(event, resetManualActivation, { once: true });
            });
        });
    });    

    if (reviewsScrollLink && reviewsTabLink) {
        reviewsScrollLink.addEventListener('click', () => reviewsTabLink.click());
    }

    //  IntersectionObserver
    const tabTargets = Array.from(document.querySelectorAll('.ds-product-tab-content'));
    initProductObserver(tabs, tabTargets);
}

