// ,       
function reviewsRating(form) {
    const ratingForm = document.querySelector(form),
        ratingStars = ratingForm.querySelectorAll('.ds-module-rating-star');

    if (!ratingStars) return;

    ratingStars.forEach((item) => {
        item.addEventListener('click', (e) => {
            const target = e.currentTarget,
                prevAll = findPrevElements(target),
                nextAll = findNextElements(target);

            nextAll.forEach(el => el.classList.remove('ds-module-rating-star-is'));
            target.classList.add('ds-module-rating-star-is');
            prevAll.forEach(el => el.classList.add('ds-module-rating-star-is'));

        });
    });
}

// ======================================================

// ,       

function updateRating(container, rating) {
    //  requestAnimationFrame    
    requestAnimationFrame(() => {
        const stars = container.querySelectorAll('.ds-module-rating-star');
        const fullStars = Math.floor(rating);
        const partialStar = rating % 1;

        if (!stars.length) return;

        stars.forEach((star, index) => {
            const innerStar = star.querySelector('.ds-module-rating-star-inner');
            if (innerStar) {
                //      
                innerStar.style.width = (index < fullStars) ? '100%' : (index === fullStars ? `${partialStar * 100}%` : '0%');
            }
        });
    });
}

