// ,     
// @@param {boolean} forceLightTheme -  true,     ,      .
//  false,       .

function switchTheme() { //     forceLightTheme  
    const themeSwitcher = document.querySelector('.ds-theme-switcher');
    const metaThemeColor = document.querySelector('[name="theme-color"]');
    const darkThemeColor = 'rgb(20, 20, 20)';
    const lightThemeColor = metaThemeColor ? metaThemeColor.getAttribute('data-oct-theme-color') : 'rgb(255, 255, 255)';

    //    
    if (!themeSwitcher) return;

    const themeSwitcherBtn = themeSwitcher.querySelectorAll('.ds-theme-switcher-btn');

    //    
    //         DOM
    //     
    function setTheme(theme, isUserClick = false) {
        //   
        themeSwitcherBtn.forEach(btn => {
            btn.classList.toggle('active', btn.classList.contains(theme));
        });

        //     body
        document.documentElement.className = `${theme}-theme`;
        
        //  - theme-color
        if (metaThemeColor) {
            metaThemeColor.setAttribute('content', theme === 'dark' ? darkThemeColor : lightThemeColor);
        }

        //     localStorage ,     
        if (isUserClick) {
            localStorage.setItem('theme', theme);
        }
    }

    //    ( DOMContentLoaded),
    //   ,      localStorage   html ,
    //    .
    //     ,       head.
    const currentActiveTheme = document.documentElement.classList.contains('dark-theme') ? 'dark' : 'light';
    setTheme(currentActiveTheme, false); //        localStorage

    //     -    
    themeSwitcherBtn.forEach(btn => {
        btn.addEventListener('click', () => {
            setTheme(btn.classList.contains('light') ? 'light' : 'dark', true); // isUserClick = true
        });
    });
}

