function octShowMoreModule(moduleId, lozadEnable = 0, buttonId, htmlModuleId, pageId, path = 0) {
    const currentPage = parseInt(document.querySelector(`#${pageId}`).value);
    const nextPage = currentPage + 1;
    const button = document.querySelector(`#${buttonId}`);
    const newProductsContainer = document.querySelector(`#${htmlModuleId}`);

    const handleAjaxResponse = (response) => {
        let wrapper = document.createElement('div');
        wrapper.innerHTML = response;

        const products = wrapper.querySelectorAll('.ds-module-col');
        products.forEach(element => newProductsContainer.insertAdjacentElement('beforeend', element));

        const checkButton = wrapper.querySelector(".oct-load-more-button-wrapper");
        if (!checkButton) {
            newProductsContainer.nextElementSibling.classList.add('d-none');
        }

        setAddedCompareWishlist();
        setCartBtnAdded();

        const ratingContainers = newProductsContainer.querySelectorAll('.ds-module-rating-stars');

		ratingContainers.forEach(container => {
			const rating = parseFloat(container.getAttribute('data-rating'));
			updateRating(container, rating);
		});
    };

    $.ajax({
        type: 'post',
        dataType: 'html',
        url: 'index.php?route=extension/module/oct_product_modules/modulePage',
        data: `module_id=${moduleId}&page=${nextPage}&oct_path=${path}`,
        cache: false,
        beforeSend: () => {
            masked('body', true);
            button.classList.add('oct-animated');
        },
        complete: () => {
            masked('body', false);
            button.classList.remove('oct-animated');
        },
        success: handleAjaxResponse,
        error: (error) => {
            //  
        }
    });

    document.querySelector(`#${pageId}`).value = nextPage;
}

