function masked(element, status) {
    if (status == true) {
        $('body').append('<div class="ds-loader-overlay d-flex justify-content-center align-items-center"><span class="ds-loader"></span></div>');
    } else {
        setTimeout(function () {
            $('.ds-loader-overlay').remove();
        }, 600);
    }
}

