// ,         .                

/**
 *       .
 *
 * @param {string}   newPriceAmount        (  )
 * @param {string|HTMLElement} target   CSS-    DOM-
 * @param {number}  durationMs = 250     ,  ()
 * @param {number}  steps      = 50       ()
 */
function animatePrice(newPriceAmount, target, durationMs = 250, steps = 50) {
    try {
        const priceBlock = typeof target === 'string'
            ? document.querySelector(target)
            : target;
        if (!priceBlock) return;

        // ----  1.    parseFloat
        const rawOld = priceBlock.textContent;
        const rawNew = newPriceAmount;

        const extract = str => ({
            raw: str.replace(/[^0-9.,]/g, ''),                 //   + , .
            value: parseFloat(str.replace(/[^0-9.,]/g, '').replace(',', '.')),
            currency: str.replace(/[0-9.,\s]/g, '').trim()
        });

        const { raw: rawOldNum, value: oldValue, currency } = extract(rawOld);
        const { raw: rawNewNum, value: newValue } = extract(rawNew);

        // ----  2.     ?
        const decimals = (() => {
            const m = rawOldNum.match(/[.,](\d+)/);
            return m ? m[1].length : 0;               // 0, 2   -
        })();

        const isPrefix = /^\D/.test(rawOld.trim());
        const hasSpace = /\d\s\D/.test(rawOld) || /\D\s\d/.test(rawOld);

        const stepTime = durationMs / steps;
        const delta = (newValue - oldValue) / steps;
        let i = 0;

        // ----  3. ,      
        const format = val =>
            val.toLocaleString('en-US', {
                minimumFractionDigits: decimals,
                maximumFractionDigits: decimals
            }).replace(/,/g, ' ');

        const render = v => isPrefix
            ? `${currency}${hasSpace ? ' ' : ''}${format(v)}`
            : `${format(v)}${hasSpace ? ' ' : ''}${currency}`;

        const timer = setInterval(() => {
            priceBlock.textContent = render(oldValue + delta * i);
            if (++i > steps) {
                clearInterval(timer);
                priceBlock.textContent = render(newValue); // 
            }
        }, stepTime);
    } catch (err) {
        console.error('animatePrice error:', err);
    }
}

// ====================================

$(function () {
/*********** Category quantity ***********/
    $(document).on("click", ".ds-plus", function () {
        if ($(this).attr('onclick')) {
            return;
        }

        const oldVal = $(this).prev().val();
        let newVal = (parseInt($(this).prev().val(), 10) + 1);
        $(this).prev().val(newVal);
        
        let priceBlock = $(this).closest('.ds-module-col').find('.ds-price-new');
        let priceBlockOld = $(this).closest('.ds-module-col').find('.ds-price-old');
        const productID = $(this).closest('.ds-module-cart').find('input[name="product_id"]').val();
        const inputElement = $(this).prev();

        $.ajax({
            type: 'post',
            url: 'index.php?route=octemplates/main/oct_functions/updatePrices',
            data: {
                product_id: productID,
                quantity: newVal,
                operation: 'plus',
                currentQuantity: inputElement.val()
            },
            cache: false,
            success: function(json) {
                if (json['quantity']) {
                    inputElement.val(json['quantity']);
                }
                
                if (json['special']) {
                    animatePrice(json['special'], priceBlock[0]);
                    animatePrice(json['price'], priceBlockOld[0]);
                } else {
                    animatePrice(json['price'], priceBlock[0]);
                }
            }
        });
    });

    $(document).on("click", ".ds-minus", function () {
        if ($(this).attr('onclick')) {
            return;
        }

        const oldVal = $(this).next().val();
        const minimum = $(this).parent().find('.min-qty').val();
        let newVal = 0;

        if (oldVal > 1) {
            newVal = (parseInt($(this).next().val(), 10) - 1);
        } else {
            newVal = 1;
        }
        if (newVal < minimum) {
            newVal = minimum;
        }

        const formInput = $(this).next();
        formInput.val(newVal);

        let priceBlock = $(this).closest('.ds-module-col').find('.ds-price-new');
        let priceBlockOld = $(this).closest('.ds-module-col').find('.ds-price-old');
        const productID = $(this).closest('.ds-module-cart').find('input[name="product_id"]').val();

        $.ajax({
            type: 'post',
            url: 'index.php?route=octemplates/main/oct_functions/updatePrices',
            data: {
                product_id: productID,
                quantity: newVal,
                operation: 'minus',
                currentQuantity: formInput.val()
            },
            cache: false,
            success: function(json) {
                if (json['quantity']) {
                    formInput.val(json['quantity']);
                }

                if (json['error']) {
                    formInput.val(json['error']);
                    return;
                }
                if (json['special']) {
                    animatePrice(json['special'], priceBlock[0]);
                    animatePrice(json['price'], priceBlockOld[0]);
                } else {
                    animatePrice(json['price'], priceBlock[0]);
                }
            }
        });
    });

    $(document).on("change", ".ds-module-quantity .form-control", function () {
        if ($(this).attr('keypress')) {
            return;
        }

        const minimum = $(this).parent().find('.min-qty').val();
        let newVal = parseInt($(this).val());
        if (newVal < minimum) {
            newVal = minimum;
        }
        const inputElement = $(this);

        let priceBlock = $(this).closest('.ds-module-col').find('.ds-price-new');
        let priceBlockOld = $(this).closest('.ds-module-col').find('.ds-price-old');
        const productID = $(this).closest('.ds-module-cart').find('input[name="product_id"]').val();

        $.ajax({
            type: 'post',
            url: 'index.php?route=octemplates/main/oct_functions/updatePrices',
            data: {
                product_id: productID,
                quantity: newVal,
                operation: 'manual',
                currentQuantity: inputElement.val()
            },
            cache: false,
            success: function(json) {
                if (json['quantity']) {
                    inputElement.val(json['quantity']);
                }
                
                if (json['special']) {
                    animatePrice(json['special'], priceBlock[0]);
                    animatePrice(json['price'], priceBlockOld[0]);
                } else {
                    animatePrice(json['price'], priceBlock[0]);
                }
            }
        });
    });

    $(document).on('click', '.ds-module-cart-btn', function () {
        const productID = $(this).parent().find('input[name="product_id"]').val();
        const quantity = $(this).parent().find('.ds-module-quantity .form-control').val();
        this.classList.remove('added');

        let requiredOptions = document.querySelector('.required');

        if (requiredOptions && requiredOptions.classList.contains('option-required')) {
            cart.add(productID, quantity, 1, this);
        } else {
            cart.add(productID, quantity, 0, this);
        }
    });

    /*********** Fixed contacts ***********/

    $('.ds_fixed_contact_button').on('click', function () {
        $(this).toggleClass('clicked');
        $('.ds-fixed-contact-dropdown').toggleClass('expanded');
        $('.ds-fixed-contact-icon .fa-envelope').toggleClass('d-none');
        $('.ds-fixed-contact-icon .ds-fixed-contact-text').toggleClass('d-none');
        $('#ds_fixed_contact_substrate').toggleClass('active');
    });

    $('#ds_fixed_contact_substrate').on('click', function () {
        $(this).removeClass('active');
        $('.ds-fixed-contact-dropdown').removeClass('expanded');
        $('.ds-fixed-contact-icon .fa-envelope').removeClass('d-none');
        $('.ds-fixed-contact-icon .ds-fixed-contact-text').toggleClass('d-none');
        $('#ds_fixed_contact_button').removeClass('clicked');
    });

    $('.ds-fixed-contact-dropdown').click(function (e) {
        e.stopPropagation();
    });
    /*********** End of Fixed contacts ***********/
});

//  setCartBtnAdded     
function setCartBtnAdded(updateOk = false) {
    //  ,    id   
    let cartElement = document.querySelector('[data-cart-ids]');

    //    ,   
    if (!cartElement) return;

    //      
    let cartButtons = document.querySelectorAll('.ds-module-cart-btn, #button-cart, #oct-popup-button-cart, .ds-product-fixed-cart-btn, .ds-category-cart-btn');

    //    ,   
    if (!cartButtons) return;

    //   id ,    .
    //  filter(Boolean),      
    let cartProducts = cartElement.getAttribute('data-cart-ids').split(',').filter(Boolean);

    //     ,   
    if (!cartProducts) return;

    //        
    let changeButtonState = (button, productId, state) => {
        //        
        let cartText = cartElement.getAttribute(state ? 'data-cart-text-in' : 'data-cart-text');

        try {
            //    
            let buttonText = button.querySelector('.button-text');
            //   
            buttonText.textContent = cartText;
        } catch (error) {}

        //   title  
        button.setAttribute('title', cartText);

        //       
        state ? button.classList.add('added') : button.classList.remove('added');
    };

    //    
    cartButtons.forEach((button) => {
        //  id    
        let productIdElement, productId;

        productIdElement = button.parentElement.querySelector('input[name="product_id"]');

        if (productIdElement) {
            productId = productIdElement.value;
        }

        //  id    ,     ""
        if (cartProducts.includes(productId)) {
            changeButtonState(button, productId, true);
        }

        //   updateOk       ,
        //     " "
        if (updateOk && !cartProducts.includes(productId)) {
            changeButtonState(button, productId, false);
        }

        //       
        button.addEventListener('click', () => {
            //    ,      
            if (!cartProducts.includes(productId)) {
                cartProducts.push(productId);

                //   data-cart-ids
                cartElement.setAttribute('data-cart-ids', cartProducts.join(','));

                //   
                changeButtonState(button, productId, true);
            }
        });
    });
}

function showProductButtons() {
    const element = document.querySelector('.ds-product-fixed-btns');

    if (!element) return;

    let observer = new IntersectionObserver(function (entries) {
        if (entries[0].isIntersecting === true) {
            element.classList.remove('enabled');
        } else {
            element.classList.add('enabled');
        }
    }, {
        rootMargin: '0px 0px 100px 0px',
        threshold: [0]
    });

    observer.observe(document.querySelector('.ds-product-main'));
}

// ========================================

//   ,   DOM

document.addEventListener('DOMContentLoaded', () => {
    if ('requestIdleCallback' in window) {
        requestIdleCallback(() => {
            //   error_style   , 
            //    ,       
            removeErrorStyle(); 
            
            // /      . 
            //      ,    .
            dropdownToggle();
            
            //     wishlist  compare
            setAddedCompareWishlist();

            //       
            setCartBtnAdded();
            
            // /  
            mobileMenu(); 
        }, { timeout: 1000 });
    } else {
        //  ,    requestIdleCallback
        removeErrorStyle();
        dropdownToggle();
        setAddedCompareWishlist();
        setCartBtnAdded();
        mobileMenu(); 
    }
    
    //   no-transition  body
    document.documentElement.classList.remove("no-transition");

    var tooltipTriggerList = [].slice.call(document.querySelectorAll('[data-bs-toggle="tooltip"]'));
    var tooltipList = tooltipTriggerList.map(function (tooltipTriggerEl) {
        return new bootstrap.Tooltip(tooltipTriggerEl);
    });

    var popoverTriggerList = [].slice.call(document.querySelectorAll('[data-bs-toggle="popover"]'));
    var popoverList = popoverTriggerList.map(function (popoverTriggerEl) {
        return new bootstrap.Popover(popoverTriggerEl, {
            container: 'body'
        });
    });

    var body = $('body');
    body.on('mouseover', '[data-bs-toggle="tooltip"]', function (e) {
        e.stopPropagation();
        return new bootstrap.Tooltip(this).show();
    });

    body.on('mouseleave', '[data-bs-toggle="tooltip"]', function (e) {
        $('[role="tooltip"]').fadeOut(function () {
            e.stopPropagation();
            $(this).remove();
        });
    });

    body.on('mouseover', '[data-bs-toggle="popover"]', function (e) {
        e.stopPropagation();
        return new bootstrap.Popover(this).show();
    });

    body.on('mouseleave', '[data-bs-toggle="popover"]', function (e) {
        $('[role="tooltip"]').fadeOut(function () {
            e.stopPropagation();
            $(this).remove();
        });
    });

    let viewedProductsCookie = getOCTViewedCookie('oct_product_views'),
        viewedMobileBadge = document.querySelector('.ds-mobile-bottom-nav-item-viewed .ds-mobile-bottom-nav-item-badge'),
        viewedDesktopBadge = document.querySelector('.ds-header-viewed-button .badge');
    
    let productOCTCount = viewedProductsCookie && viewedProductsCookie.length > 0 ? viewedProductsCookie.split('%2C').length : 0;
    
    if ((viewedMobileBadge || viewedDesktopBadge) && productOCTCount > 0) {
        viewedMobileBadge.textContent = productOCTCount;
        viewedDesktopBadge.textContent = productOCTCount;
    }

    document.addEventListener('hide.bs.modal', function (event) {
        document.activeElement.blur();
    });

});

